import { Component, getAssetPath, h, Host, Prop } from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';

@Component({
  tag: 'tp-question',
  styleUrl: 'question.scss',
  assetsDirs: ['assets'],
  shadow: true,
})
export class TpQuestion {
  private iconDown = getAssetPath('./assets/tp-chevron-down.svg');

  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true, mutable: true }) collapsed = false;

  /**
   * todo
   */
  @Prop() texto?: string;

  /**
   * todo
   */
  toggle() {
    this.collapsed = !this.collapsed;
  }

  render() {
    const { collapsed, texto, color, iconDown } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-question': true,
          'tp-question--collapsed': collapsed,
        })}
        onClick={() => {
          this.toggle();
        }}
      >
        {texto && <div class="tp-question__text" innerHTML={texto}></div>}
        <div class="tp-question__text">
          <slot></slot>
        </div>
        <ion-icon class="tp-question__icon" src={iconDown}></ion-icon>
      </Host>
    );
  }
}
